import React, { Component } from 'react';
import Slider from "react-slick";
import {Row , Container , Col} from 'react-bootstrap';
import Portfolio1 from '../img/portfolio1.png';
import Portfolio2 from '../img/portfolio2.png';
import Portfolio3 from '../img/portfolio3.png';
import Portfolio4 from '../img/portfolio4.jpg';



class projectSection extends Component {
    state = {  }
    render() {
        const settings = {
            dots: false,
            arrows : false,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            speed: 3000,
            autoplaySpeed: 3000,
            responsive: [
                {
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                  }
                },
                {
                  breakpoint: 600,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                  }
                }
              ]
            
          };
        return (
            <div>
        <Container>
                          <Row className="parentGrid" id="portfolio">
                              <Col xs={12}>
                                   <div className="projectTitle">OUR COMPLETED PROJECTS</div>
                                   <div className="projectHeader"><h3>Recent Projects</h3></div>
                                   
                              </Col>
                              <Col>
                                    
                              </Col>
                            </Row>
                            </Container>
                <Container className="sliderParent">
        
                <Slider {...settings}>
                <a href={"https://www.craftcollaz.com/"} target="_blank"><div  className="sliderContent">
                <div className="sliderImg"><img
                                            src={Portfolio1}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Handicrafts Seling Website</h4></div>
                    <div className="sliderpara"><p>Tech Stack : Wordpress, Payment Gateway, Mobile App</p></div>
                    </div>
                </div ></a>
                <a href={"https://tajtravels.in"} target="_blank"><div  className="sliderContent">
                <div className="sliderImg"><img
                                            src={Portfolio2}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Online Tour Planing Website </h4></div>
                    <div className="sliderpara"><p>Tech Stack : Wordpress, Tour Booking System, Mobile App</p></div>
                    </div>
                </div ></a>
                <a href={"https://www.i-o-e.in/"} target="_blank"><div className="sliderContent">
                <div className="sliderImg"><img
                                            src={Portfolio3}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Surveillance E - Com Website</h4></div>
                    <div className="sliderpara"><p>Tech Stack : Wordpress, Woocommerce</p></div>
                    </div>
                </div></a>
                <a href={"https://anusuyaindia.com/"} target="_blank"><div  className="sliderContent">
                <div className="sliderImg"><img
                                            src={Portfolio4}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Real E-State Website</h4></div>
                    <div className="sliderpara"><p>Tech Stack : Bootstrap 4, HTML / CSS / JS</p></div>
                    </div>
                </div ></a>
                </Slider>
        </Container>
        <div className="space"></div>
                </div>
            
        );
    }
}

export default projectSection;