import React, { Component } from 'react';
import {Row , Container , Col} from 'react-bootstrap';
import { Link, animateScroll as scroll } from "react-scroll";
class heroSection extends Component {
    state = {  }
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col xs={12} sm={6}>
                            <div>
                                <div className="heroParentTitle">
                                <div className="heroTitle">
                                <h2 >
                                        Your Potential Our Passion.
                                </h2>
                                </div>
                                </div>
                                <p className="heroPara">
                                GENWEBTECHNOLOGY is a full service marketing agency based in Bhubaneswar, India.
                                We specialise in creative solutions that empowers start-ups and small businesses to
                                Communicate their message effectively and connect people to go after a common vison.

                                </p>
                                <div className="heroButton">
                                <Link
                                                    activeClass="active"
                                                    to="portfolio"
                                                    spy={true}
                                                    smooth={true}
                                                    offset={-70}
                                                    duration={500}
                                                        >
                                    See Portfolio
                                    </Link>
                                </div>
                            </div>

                        </Col>
                                
                        <Col xs={12} sm={6}>
                        <div className="heroImg"></div>
                        </Col>
                    </Row>
                    <div className="space"></div>
                </Container>

            </div>
            
        );
    }
}

export default heroSection;
