import React, { Component } from 'react';
import Logo from '../img/logo.png'
import {Row , Container , Col, Navbar , Nav, NavDropdown} from 'react-bootstrap';
import { Link, animateScroll as scroll } from "react-scroll";

class topbar extends Component {
    state = {  }
    scrollToTop = () => {
        scroll.scrollToTop();
      };
    render() {
        return (
            <div>
                        <Container fluid >
                            <Row className="topbar">
                                    <Col lg={3} sm={6} className="topbar-social">
                                        <i className='fa fa-facebook icon' ></i>
                                        <i className='fa fa-pinterest-p icon' ></i>
                                        <i className='fa fa-github icon' ></i>
                                    </Col>
                                    <Col lg={9} sm={6} xs={12} className="topbar-contact">
                                         {/* <div ><i className='fa fa-phone ' ></i>969999000</div> */}
                                        <div ><i className='fa fa-envelope '></i>genwebtechnology@gmail.com</div>  
                                    </Col> 
                            </Row>
                            
                        <Navbar collapseOnSelect expand="lg" bg="transparment" variant="dark">
                                    <Navbar.Brand href="#home">
                                    <img
                                            src={Logo}
                                       
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" />

                                    </Navbar.Brand>
                                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                                        <Navbar.Collapse id="responsive-navbar-nav">
                                                <Nav className="ml-auto">
                                                <Nav.Link href="#" >
                                                
                                                    Home
                                                    
                                                    </Nav.Link>
                                                <Nav.Link  >
                                                <Link
                                                    activeClass="active"
                                                    to="service"
                                                    spy={true}
                                                    smooth={true}
                                                    offset={-70}
                                                    duration={500}
                                                        >
                                                    Service
                                                    </Link>
                                                </Nav.Link>
                                                <Nav.Link  >
                                                <Link
                                                    activeClass="active"
                                                    to="portfolio"
                                                    spy={true}
                                                    smooth={true}
                                                    offset={-70}
                                                    duration={500}
                                                        >
                                                    Portfolio
                                                    </Link>
                                                </Nav.Link>
                                                <Nav.Link  >
                                                <Link
                                                    activeClass="active"
                                                    to="aboutUs"
                                                    spy={true}
                                                    smooth={true}
                                                    offset={-70}
                                                    duration={500}
                                                        >
                                                    About
                                                    </Link>
                                                </Nav.Link>
                                                <Nav.Link  >
                                                <Link
                                                    activeClass="active"
                                                    to="contactUs"
                                                    spy={true}
                                                    smooth={true}
                                                    offset={-70}
                                                    duration={500}
                                                        >
                                                    Contact
                                                    </Link>
                                                </Nav.Link>
                                                </Nav>
                                        </Navbar.Collapse>
                        </Navbar>
                                    
                           
                            </Container>
                            
                    
            </div>
            
        );
    }
}

export default topbar;
